.button_container{
margin: 0;
padding: 0;
}

@mixin button__base ($background, $width, $border) {
height: 44px;
text-align: center;
text-decoration: none;
border-radius: 22px;
border: $border;
background: $background;
width: $width;
}


.button:hover{
opacity: 0.5;
}

.button_text{
color: white;
}

